<template>
	<view class="container">
		<u-form :model="form" ref='myForm' >
			<u-form-item label="收货人" prop="name" label-width="140rpx">
			    <u-input v-model="form.name" placeholder="名字" :border='true'></u-input>
			</u-form-item>
		    <u-form-item label="账号" prop="phoneNum" label-width="140rpx">
		        <u-input v-model="form.phoneNum" placeholder="手机号" :border='true'></u-input>
		    </u-form-item>
			<u-form-item label="所在地区" label-width="140rpx" prop="area">
				<city-select v-model="value" @city-change="cityChange"></city-select>
				<!-- <view class="u-demo-result-line" @click="value = true">{{ input ? input : '省、市、区' }}</view> -->
				<u-input v-model="form.area" :border='true' placeholder="省/市/区" @click="value = true"></u-input>
			</u-form-item>
			<u-form-item label="详细地址" label-width="140rpx" prop="address">
				<u-input v-model="form.address" type="textarea" :border='true' :height="20" :auto-height="false" placeholder="小区楼栋/乡村名称" />
			</u-form-item>
		    <u-form-item label="设为默认收货地址" label-width="240rpx"><u-switch slot="right" v-model="switchVal"></u-switch></u-form-item>
		</u-form>
		<button class="register" @click="submit">保存</button>
	</view>
</template>
<script>
	import citySelect from './u-city-select.vue';
	export default {
		components: {
			citySelect
		},
		data() {
			return {
				value: false,
				// input: '',
				switchVal:false,
				id:'',
				form: {
					name: "",
				    phoneNum: "",
					area:'',
					address:''
				},
				rules: {
					name: [
						{ 
							required: true, 
							message: "请填写收货人", 
							trigger: ['change','blur'],
						},
						
					],
					phoneNum: [
						{ required: true, message: '请填写正确的手机号', trigger: ['change','blur'] },
						{
							// 自定义验证函数，
							validator: (rule, value, callback) => {
								return this.$u.test.mobile(value);
							},
							message: '请填写正确的手机号',
							// 触发器可以同时用blur和change
							trigger: ['change','blur'],
						},
					],
					area: [
						{ 
							required: true, 
							// message: "请选择所在地区", 
							// trigger: ['blur'],
						},
						
					],
					address: [
						{ 
							required: true, 
							message: "请填写详细地址", 
							trigger: ['change','blur'],
						},
						
					],
				}
			}
		},
		methods: {
			cityChange(e) {
				this.form.area = e.province.label + ' ' + e.city.label + ' ' + e.area.label;
			},
			submit() {
			    this.$refs.myForm.validate(async valid => {
			        if (valid) {
						try{
							const params = {
								name: this.form.name,
								phone: this.form.phoneNum,
								area:this.form.area,
								address:this.form.address,
								isDefault:this.switchVal,
								id:this.id
							};
							// params.id = Date.now()
							await this.$store.dispatch('addAddress', params)
							// 修改成功
							this.$u.route({
								url:'pages/order/address',
								type:'redirect'
							})
						}catch(e){
							this.$u.toast(e)
						}
					}
				})
			}
		},
		onReady() {
		    this.$refs.myForm.setRules(this.rules)
		},
		onLoad(option) {
			// console.log(option);
			this.form.name = option.name;
			this.form.phoneNum = option.phone;
			this.form.area = option.area;
			this.form.address = option.address;
			this.switchVal = option.isDefault == 'true'? true : false;
			// console.log(this.switchVal)
			this.id = option.id;
		}
	}
</script>
<style lang="scss" scoped>
	.container {
		u-form-item {
			margin-left: 20rpx;
		}
		
	}
</style>